export const initial = () => async (
    dispatch: AppDispatch, getState: AppGetState
) => {




    debugger
    const currentDepartmentMode = await localAPI.getParsedData('currentDepartmentMode')

    if (currentDepartmentMode) {
        if (currentDepartmentMode.code) {
            dispatch(
                departmentActions
                    .setMode({ code: currentDepartmentMode.code })
            )
        }
    }

    console.log('app initial')
    //@ts-ignore
    // console.log(window.BX24)



    const state = getState()

    const currentRoute = state.router.current

    const fetchedDdomain = __IN_BITRIX__ ? await bitrixAPI.getDomain() : null
    let domain = fetchedDdomain ? fetchedDdomain : TESTING_DOMAIN
    console.log('domain')
    //@ts-ignore
    console.log(domain)

    let companyPlacement = {
        placement: 'COMPANY',
        options: {
            ID: 0
        }
    } as Placement
    let currentCompany = null
    let currentDeal = null


    const placement = __IN_BITRIX__
        ? await bitrixAPI.getPlacement() as Placement | null
        : TESTING_PLACEMENT
    const user = __IN_BITRIX__ ? await bitrixAPI.getCurrentUser() as BXUser : TESTING_USER
    // await auth.getSanctumTest()


    console.log('app placement')

    console.log(placement)

    const entitiesFromPlacement = await getEntitiesFromPlacement(
        placement,
        domain
    ) as EntitiesFromPlacement
    // if (placement.placement && placement.options && placement.options.ID) {
    //     const currentEntityPlacementType = placement.placement
    //     if (currentEntityPlacementType.includes("DEAL")) {
    //         // console.log('in deal')
    //         const currentEntities = __IN_BITRIX__ && await bitrixAPI
    //             .getDealAndCompany(placement.options.ID, null)

    //         currentCompany = currentEntities.company
    //         currentDeal = currentEntities.deal
    //         companyPlacement.options.ID = currentCompany.ID

    //     } else if (currentEntityPlacementType.includes("COMPANY")) {

    //         // console.log('in company')
    //         const currentEntities = __IN_BITRIX__ && await bitrixAPI
    //             .getDealAndCompany(null, placement.options.ID)


    //         currentCompany = currentEntities.company
    //         currentDeal = currentEntities.deal
    //         companyPlacement.options.ID = placement.options.ID

    //     }
    // }
    currentCompany = entitiesFromPlacement.currentCompany
    currentDeal = entitiesFromPlacement.currentDeal
    companyPlacement = entitiesFromPlacement.companyPlacement
    const currentTask = entitiesFromPlacement.currentTask
    // const isDetail = isDetailPlacement(placement)
    // const isActivity = isActivityPlacement(placement)
    // const isTask = isTaskPlacement(placement)
    const displayMode = getDisplayMode(placement)
    
    if (displayMode == APP_DISPLAY_MODE.ENTITY_CARD) {
        __IN_BITRIX__ && await bitrixAPI.getFit()
    }

    dispatch(
        appActions.
            setAppData(
                {
                    domain,
                    user,
                    placement: companyPlacement,
                    company: currentCompany,
                    deal: currentDeal,
                    display: displayMode,
                    // isDetail,
                    // isTask,
                    task: currentTask

                }
            ))



    const data = {
        domain: domain || TESTING_DOMAIN,
        userId: user['ID'] || DEV_CURRENT_USER_ID,
        placement: companyPlacement,
        currentTaskId: currentTask?.id

    }

    console.log('get tasks data')
    console.log(data)

    // dispatch(initialEventTasks())
    dispatch(taskAPI.endpoints.fetchTasks.initiate(data));


    dispatch(getDepartment())
    dispatch(
        appActions
            .setInitializedSuccess({})
    )

    const getPortaldata = {
        domain: domain || TESTING_DOMAIN,
        userId: user['ID'],

    }
    dispatch(portalAPI.endpoints.fetchPortal.initiate({ domain }));


    // dispatch(setInitialRoute(APP_TYPE.CALLING, false))
    // dispatch(setPreloader(false))

}


