import { FC, useState } from "react"
import { EV_REPORT_PROP } from "../../EventReport/type/event-report-type"
import { useAppDispatch, useAppSelector } from "@/app/lib/hooks/redux"
import { eventReportActions } from "../../EventReport"
import EVCard from "@/shared/ui/Cards/EventEntityCard/EventCard"
import AText from "@/shared/ui/Inputs/Text/AText"
import { eventActions } from "@/processes/event"


export const Comment: FC = () => {
    const [isRequiredError, setIsRequiredError] = useState(false)

    const handleOnNameFocus = () => {
        if (isRequiredError) {
            setIsRequiredError(false)
        }


    }

    const dispatch = useAppDispatch();
    const error = useAppSelector(state => state.event.errors.current.comment)

    const handleChange = (value: string) => {
        if (error) {
            dispatch(
                eventActions
                    .setError(
                        {
                            code: EV_REPORT_PROP.COMMENT,
                            value: ''
                        }
                    )
            )
        }
        dispatch(
            eventReportActions
                .setReportProp(
                    {
                        propName: EV_REPORT_PROP.COMMENT,
                        value
                    }
                )
        )
    }
    return (
        <EVCard title={'Комментарий'} width={12} size="big">

            <AText
                label={'Опишите кратко что было'}
                height={7}
                handleChange={handleChange}
                nameForHandler={EV_REPORT_PROP.COMMENT}
                errorMessage={error}
            />



        </EVCard>

    )
}
