
import { callMethod } from 'bx24-api';

export const bitrixActivityAPI = {

    setActivity: async (companyId) => {
        let result = null
        let dealResponse = null
        const fields = {
            "RESPOSIBLE_ID": 1
        }
        // https://dev.1c-bitrix.ru/rest_help/crm/rest_activity/configurable/structure/actiondto.php
        const layout = {

            "icon": {
                "code": "call-completed"
            },
            "header": {
                "title": "Входящий звонок",
                "tags": {
                    "status2": {
                        "type": "warning",
                        "title": "не расшифрован"
                    }
                }
            },
            "body": {
                "logo": {
                    "code": "call-incoming",
                    "action": {
                        "type": "redirect",
                        "uri": "/crm/deal/details/123/"
                    }
                },
                "blocks": {
                    "client": {
                        "type": "withTitle",
                        "properties": {
                            "title": "Клиент",
                            "inline": true,
                            "block": {
                                "type": "text",
                                "properties": {
                                    "value": "ООО Рога и Копыта"
                                }
                            }
                        }
                    },
                    "responsible": {
                        "type": "lineOfBlocks",
                        "properties": {
                            "blocks": {
                                "client": {
                                    "type": "link",
                                    "properties": {
                                        "text": "Сергей Востриков",
                                        "bold": true,
                                        "action": {
                                            "type": "redirect",
                                            "uri": "/crm/lead/details/789/"
                                        }
                                    }
                                },
                                "phone": {
                                    "type": "text",
                                    "properties": {
                                        "value": "+7 999 888 7777"
                                    }
                                }
                            }
                        }
                    }
                }
            },
            "footer": {
                "buttons": {
                    "startCall": {
                        "title": "Редактировать",
                        "action": {
                            "type": "openRestApp",
                            "actionParams": {
                                "companyId": companyId,
                                "someImportant": "qwerty",
                                'placement': 'COMPANY',
                            },
                            "sliderParams": {
                                "labelText": "это приложение",
                                "labelColor": "#c3b3ff",
                                "labelBgColor": "violet",
                                "title": "Отчитаться",
                                "width": 1000
                            },
                        },
                        // {
                        //     "type": "openRestApp",
                        //     "actionParams": {
                        //         "clientId": companyId
                        //     }
                        // },
                        "type": "primary"
                    }
                },
                "menu": {
                    "showPostponeItem": "false",
                    "items": {
                        "confirm": {
                            "title": "Подтвердить заявку",
                            "action": {
                                "type": "restEvent",
                                "id": "confirm",
                                "animationType": "loader"
                            }
                        },
                        "decline": {
                            "title": "Отклонить заявку",
                            "action": {
                                "type": "restEvent",
                                "id": "decline",
                                "animationType": "loader"
                            }
                        }
                    }
                }
            }
        }
        const data = {
            ownerTypeId: 4,
            ownerId: companyId,
            fields,
            layout

        }

        try {
            dealResponse = await callMethod(
                'crm.activity.configurable.add',
                data
            );
            console.log('setActivity')
            console.log('dealResponse')

            if (dealResponse) {
                if (dealResponse.answer && dealResponse.answer.result) {
                    result = dealResponse.answer.result

                }


            }
            return result
        } catch (error) {
            console.log('error')
            console.log(dealResponse)
            console.log(error)
            return result
        }

    },


    setInformationPresentationInit: async (link,companyId, responsibleId, name) => {
        let result = null
        let dealResponse = null
        const fields = {
            "RESPOSIBLE_ID": responsibleId
        }
        // https://dev.1c-bitrix.ru/rest_help/crm/rest_activity/configurable/structure/actiondto.php
        const layout = {

            "icon": {
                "code": "call-completed"
            },
            "header": {
                "title": "Заявка на презентацию",
                // "tags": {
                //     "status2": {
                //         "type": "warning",
                //         "title": "не расшифрован"
                //     }
                // }
            },
            "body": {
                "logo": {
                    "code": "call-incoming",
                    "action": {
                        "type": "redirect",
                        "uri": link
                    }
                },
                "blocks": {
                    "client": {
                        "type": "withTitle",
                        "properties": {
                            "title": "Заявка",
                            "inline": true,
                            "block": {
                                "type": "link",
                              
                                "properties": {
                                    "value": name,
                                    "url": link,
                                }
                            }
                        }
                    },
                 
                }
            },
            "footer": {
                "buttons": {
                    "startCall": {
                        "title": "Редактировать",
                        "action": {
                            "type": "openRestApp",
                            "actionParams": {
                                "companyId": companyId,
                                "someImportant": "qwerty",
                                'placement': 'COMPANY',
                            },
                            "sliderParams": {
                                "labelText": "это приложение",
                                "labelColor": "#c3b3ff",
                                "labelBgColor": "violet",
                                "title": "Отчитаться",
                                "width": 1000
                            },
                        },
                        // {
                        //     "type": "openRestApp",
                        //     "actionParams": {
                        //         "clientId": companyId
                        //     }
                        // },
                        "type": "primary"
                    }
                },
                "menu": {
                    "showPostponeItem": "false",
                    "items": {
                        "confirm": {
                            "title": "Посмотреть заявку",
                            "action": {
                                "type": "redirect",
                                "id": "confirm",
                                "uri": link
                            }
                        },
                        "decline": {
                            "title": "Редактировать заявку",
                            "action": {
                               "type": "redirect",
                                "id": "decline",
                                "url": link
                            }
                        }
                    }
                }
            }
        }
        const data = {
            ownerTypeId: 4,
            ownerId: companyId,
            fields,
            layout

        }

        try {
            dealResponse = await callMethod(
                'crm.activity.configurable.add',
                data
            );
            console.log('setActivity')
            console.log('dealResponse')

            if (dealResponse) {
                if (dealResponse.answer && dealResponse.answer.result) {
                    result = dealResponse.answer.result

                }


            }
            return result
        } catch (error) {
            console.log('error')
            console.log(dealResponse)
            console.log(error)
            return result
        }

    },


}


